:root{
	--bg-color-white: #FFFFFF;
	--color-black: #000;
	--color-orange: #FF8F52;
	--bg-color-main: #F2F2F2;
	--color-grey: #838383;
	--bg-color-footer: #454545;
	--footer-color-text: #d9d9d9;
	--border-input: #d9d9d9;
	--color-lite-black: #121212;
	--bg-color-menu: rgba(255,255, 255, 1);

	// 252525 фон
	// 121212 хедер

	// lh-del
	--h1-lh: 116%; 
	--promo-text-lh: 115%; 
	--btn-orange-lh: 116%; 
	--post-user-name-lh: 118%; 
	--post-user-data-lh: 118%; 
	--post-title-lh: 115%; 
	--post-desc-lh: 156%; 
	--post-active-lh: 116%; 
	--post-btn-lh: 116%; 
	--comment-user-name-lh: 118%; 
	--comment-des-lh: 150%; 
	--footer-title-lh: 118%; 
	--footer-li-lh: 166%; 

	// line-height
	--lh-min: 115%;
	--lh: 116%;
	--lh-2: 118%;
	--lh-3: 153%;
	--lh-big: 166%;

	// font-size
	--fz-min: 12px;
	--fz: 18px;
	--fz-2: 16px;
	--fz-3: 20px;
	--fz-4: 22px;
	--fz-5: 32px;
	--fz-max: 55px;
}


body{
	font-size: var(--fz);
	line-height: var(--lh);
	background-color: var(--bg-color-main);
	&._lock{
		overflow: hidden;
	}
	&::-webkit-scrollbar {
		width: 0;
	}
}

._container{
	max-width: 1245px;
	margin: 0 auto;
	padding: 0 15px;
}

.app{
	display: flex;
	flex-direction: column;
	height: 100%;
}

main{
	flex: 1 1 auto;
	margin: 140px 0px 0px;
}

.modal{
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, .2);
	display: flex;
	justify-content: center;
	align-items: center;
	opacity: 0;
	visibility: hidden;
	&._active{
		opacity: 1;
		visibility: visible;
	}
}

